	module.exports = {"data":{"post":{"html":"<p>Yech. The ice and snow had been gone for 3 weeks, until today. The snow returned, with wind and temperatures near freezing, just in time for evening rush hour. My 60 minute commute from work to day care and home took nearly 3 hours.</p>\n<p>Christopher's grandparents will be disappointed to hear that the OshKosh overalls they gave him for Christmas already have a hole worn in the knee. They are the only pants he ever wants to wear, so I guess we will have to find a way to patch them from the inside. He doesn't like patches on the outside.</p>","fields":{"title":"Return of Winter","date":"30 Jan 2003","url":"https://papascott.de/2003-01-30-return-of-winter/"}}},"pathContext":{"slug":"/2003-01-30-return-of-winter/","prev":{"fields":{"title":"Ding Dong","date":"30 Jan 2003","slug":"/2003-01-30-ding-dong/"}},"next":{"fields":{"title":"Put It In A Letter","date":"31 Jan 2003","slug":"/2003-01-31-put-it-in-a-letter/"}}}}

